body, #root, #router-root {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.center-container {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  -webkit-transition: background-color 0.1s;
  -o-transition: background-color 0.1s;
  transition: background-color 0.1s;
}

.center {
  margin: auto;
  text-align: center;
}



